.footer-container {
  background-color: #fff;
  padding: 20px; /* Add padding for spacing */
  text-align: center;
  border-top: 1px solid #e5e5e5;
  font-family: 'Arial', sans-serif;
  position: relative; /* Ensure proper positioning */
  z-index: 30; /* Keep it above other elements */
}

.footer-logo {
  display: flex;
  justify-content: center;
  margin-bottom: 30px;
}

.footer-logo-image {
  width: 800px; /* Adjust this value to make the logo smaller */
  height: auto; /* Keep aspect ratio */
  max-width: 100%; /* Ensures responsiveness */
}

.social-icons {
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
}

.social-icon {
  width: 90px; /* Adjust the size as needed */
  height: 90px; /* Adjust the size as needed */
  border-radius: 50%; /* Make the image circular */
  margin: 0 10px; /* Space between icons */
}

.footer-info {
  font-size: 1.2rem;
  color: #333;
  margin-bottom: 20px;
}

.footer-time {
  display: block;
  font-size: 2rem;
  font-weight: bold;
  color: #333;
  margin-top: 10px;
}

.footer-buttons {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 20px;
  margin-top: 30px;
}

.footer-button {
  font-size: 1rem;
  color: #333;
  padding: 15px;
  border: 1px solid #333;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.footer-button:hover {
  color: #0073e6;
  border-color: #0073e6;
}

.footer-copyright {
  margin-top: 40px;
  font-size: 0.875rem;
  color: #666;
}

.h-footer-update-button {
  background-color: #2962FF;
  color: white;
  padding: 12px 25px;
  font-size: 1rem;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.h-footer-update-button:hover {
  background-color: #00337a;
}

.v-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.75); /* Dark overlay */
  z-index: 100;
  animation: fadeIn 0.3s forwards;
}

.v-modal-content {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: #fff;
  padding: 30px;
  max-width: 500px;
  width: 100%;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
  border-radius: 10px;
  text-align: center;
  animation: fadeIn 0.3s forwards;
}

.v-modal-close-button {
  padding: 10px 20px;
  background-color: #2962FF;
  color: white;
  border: none;
  border-radius: 8px;
  cursor: pointer;
}

@keyframes fadeIn {
  0% {
      opacity: 0;
  }
  100% {
      opacity: 1;
  }
}


/* New styles for small close button */
.small-close-button {
  padding: 5px 10px; /* Smaller padding */
  position: absolute; /* Positioning */
  top: 10px; /* Distance from the top */
  right: 10px; /* Distance from the right */
  font-size: 0.875rem; /* Smaller font size */
  background-color: transparent; /* Transparent background */
  color: #2962FF; /* Color matching the modal theme */
  border: 1px solid #2962FF; /* Optional border */
}

.small-close-button:hover {
  background-color: rgba(41, 98, 255, 0.1); /* Hover effect */
}

.download-link {
  display: inline-block;
  margin-top: 20px;
  padding: 10px 20px;
  background-color: #2962FF;
  color: white;
  border-radius: 8px;
  text-decoration: none;
}

.download-link:hover {
  background-color: #2395ff;
}
.update-history-table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 20px;
}

.update-history-table th,
.update-history-table td {
  border: 1px solid #ccc;
  padding: 8px;
  text-align: left;
}

.update-history-table th {
  background-color: #f2f2f2;
}

.update-history-table tr:nth-child(even) {
  background-color: #f9f9f9;
}

.story-button{
  background-color: #ff0000;
  color: white;
  padding: 5px 5px;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: background-color 0.3s ease;

}

.story-button:hover {
  background-color: #a00303;
}