.image-viewer-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.image-viewer-content {
  position: relative;
  max-width: 90%;
  max-height: 90%;
}

.image-viewer-image {
  width: 100%;
  height: auto;
  transition: transform 0.3s ease;
}

.image-viewer-close {
    position: absolute; /* Changed to absolute positioning */
    bottom: 20px; /* Position from the bottom */
    left: 75%; /* Center horizontally */
  background: rgba(0, 0, 0, 0.7);
  color: aliceblue;
  border: none;
  border-radius: 5px;
  padding: 10px;
  bottom: 20px; 
  cursor: pointer;
  z-index: 1100;
}

.image-viewer-nav {
  position: absolute; /* Changed to absolute positioning */
  bottom: 20px; /* Position from the bottom */
  left: 50%; /* Center horizontally */
  transform: translateX(-50%); /* Centering offset */
  display: flex;
  flex-direction: column;
  align-items: center;
  bottom: 20px; 
  gap: 5px; /* Adjust space between buttons */
}

.image-viewer-zoom-in,
.image-viewer-zoom-out {
  background: rgba(0, 0, 0, 0.7);
  border: none;
  color: white;
  border-radius: 5px;
  padding: 10px;
  cursor: pointer;
}

.image-viewer-pan-buttons {
  display: flex;
  gap: 5px; /* Adjust space between pan buttons */
}

.image-viewer-pan-buttons button {
  background: rgba(0, 0, 0, 0.7);
  color: white;
  border: none;
  border-radius: 5px;
  padding: 10px;
  bottom: 20px; 
}

/* Responsive styles */
@media (min-width: 768px) {
  .image-viewer-nav {
    flex-direction: row; /* Row layout for tablets and desktops */
    bottom: 20px; 
  }

  .image-viewer-zoom-in,
  .image-viewer-zoom-out {
    margin: 0 10px; /* Space between zoom buttons */
    bottom: 20px; 
  }
}
